header {
    margin: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px
}

#name {
    font-family: 'Bebas Neue', cursive;
    font-size: 48px;
    letter-spacing: 3px;
    position: absolute;
    left: 0;
    background-color: transparent;
}

a {
    background-color: transparent;
    margin: 0px 20px 0px 20px;
    text-decoration: none;
    position: relative;
    caret-color: transparent;
    padding: 5px;

}

a::before {
    content: "";
    position: absolute;
    display:  block;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #000;
    transform: scaleX(0);
    transition: transform 0.3s ease;
}

a:hover::before {
    transform: scaleX(1);
}

a:hover {
    color: #000
}

a:link {
    color: black
}

a:visited {
    color: black
}

#body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh
}

#body > img {
    width: 800px;
    position: relative;
    z-index: 0;
    animation: spin 2s infinite linear;
    background-color: transparent;
}

#body > h1 {
    position: absolute;
    z-index: 1;
    background-color: transparent;
    color: white;
    font-size: 100px;
    text-shadow: 0px 0px 20px black;
}

#body > h3 {
    position: absolute;
    z-index: 1;
    background-color: transparent;
    color: white;
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}